<template>
  <div class="card-container">
    <div class="mx-auto login-title text-center">
      <h1>A Web-Based COVID-19 VACCINE INVENTORY WITH DISTRIBUTION MONITORING SYSTEM</h1>
    </div>
    <v-card width="400" class="py-16 px-6 rounded-xl mx-auto">
      <validation-observer
        ref="observer"
        v-slot="{ invalid }"
      >
        <v-card-title class="justify-center">
          <span>Welcome User!</span>
        </v-card-title>

        <v-card-text>
          <v-alert
            v-model="alert.show"
            border="left"
            dismissible
            text
            :type="alert.type"
          >
            {{ alert.message }}
          </v-alert>

          <validation-provider
            name="Email"
            v-slot="{ errors }"
            rules="required|email"
          >
            <v-text-field
              label="Email"
              v-model="form.email"
              placeholder="example@email.com"
              :error-messages="errors"
              :disabled="submitting"
              outlined
            />
          </validation-provider>

          <validation-provider
            name="Password"
            v-slot="{ errors }"
            rules="required"
          >
            <v-text-field
              label="Password"
              type="password"
              v-model="form.password"
              :error-messages="errors"
              :disabled="submitting"
              outlined
            />
          </validation-provider>
        </v-card-text>

        <v-card-actions class="justify-center">
          <v-btn
            color="green"
            width="150px"
            :disabled="invalid || submitting"
            :dark="!(invalid || submitting)"
            :loading="submitting"
            rounded
            @click="submit"
          >
            Login
          </v-btn>
        </v-card-actions>
      </validation-observer>
    </v-card>
  </div>
</template>

<script>
import {required, email} from 'vee-validate/dist/rules';
import {extend, ValidationObserver, ValidationProvider, setInteractionMode} from 'vee-validate'
import axios from 'axios'

setInteractionMode('eager')

extend('required', {
  ...required,
  message: '{_field_} cannot be empty',
})

extend('email', {
  ...email,
  message: 'Invalid email format'
})

export default {
  name: 'UserLoginCard',
  components: {
    ValidationProvider,
    ValidationObserver,
  },
  data: () => ({
    alert: {
      show: false,
      type: 'error',
      message: 'asdf',
    },
    form: {
      email: '',
      password: '',
    },
    submitting: false,
  }),
  methods: {
    async submit() {
      this.submitting = true

      try {
        const apiHost = process.env.VUE_APP_API_HOST
        const data = {
          ...this.form,
          type: 'team_lead'
        }
        const result = await axios.post(`${apiHost}/users/login`, data)

        localStorage.setItem('dms-token', result.data.token)
        this.$router.push('/user/dashboard')
      } catch (error) {
        if (error.response.status === 400) this.alert.message = 'Email or password is Invalid.'
        if (error.response.status >= 500) this.alert.message = 'Internal Server Error Occured.'

        this.alert.show = true
      }

      this.submitting = false
    }
  }
}
</script>

<style scoped>
.card-container {
  width: fit-content;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}

.login-title {
  width: 80%;
  text-shadow: 0 0 8px white;
}
</style>
